<style>
  
</style>

<template>
  <div>
    <div class="search-form-wraper">
      <el-form :model="searchContent" label-position="top">
        <el-form-item label="注册开始时间">
          <el-date-picker
            class="search-input" 
            v-model="searchContent.registerStartDate"
            type="datetime"
            placeholder="注册开始时间"
            format="yyyy-MM-dd HH:mm:ss"
            value-format="yyyy-MM-dd HH:mm:ss"
            size="small"
            @change="doSearchHandel(true)">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="注册结束时间">
          <el-date-picker
            class="search-input" 
            v-model="searchContent.registerEndDate"
            type="datetime"
            placeholder="注册结束时间"
            format="yyyy-MM-dd HH:mm:ss"
            value-format="yyyy-MM-dd HH:mm:ss"
            size="small"
            @change="doSearchHandel(true)">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="会员手机号">
          <el-input class="search-input" v-model.trim="searchContent.phone" placeholder="会员手机号" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>
        <el-form-item label="注册门店">
          <el-select class="search-input" v-model="searchContent.storeNo" filterable placeholder="请选择注册门店" clearable size="small" @change="doSearchHandel(true)">
            <el-option
              v-for="item in storeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <!-- <el-form-item label="累计消费金额（大于）">
          <el-input class="search-input" v-model.trim="searchContent.minSaleAmount" placeholder="累计消费金额" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>
        <el-form-item label="累计消费金额（小于）">
          <el-input class="search-input" v-model.trim="searchContent.maxSaleAmount" placeholder="累计消费金额" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item> -->
        <el-form-item label="会员编码">
          <el-input class="search-input" v-model.trim="searchContent.id" placeholder="会员编码" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>
        <el-form-item label="当前积分（大于）">
          <el-input class="search-input" v-model.trim="searchContent.minPoint" placeholder="当前积分" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>
        <el-form-item label="当前积分（小于）">
          <el-input class="search-input" v-model.trim="searchContent.maxPoint" placeholder="当前积分" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
          <el-button @click="exportData" size="mini" :loading="exportLoading">导出</el-button>
        </el-form-item>
      </el-form>
    </div>

    <div class="table-wraper">
      <div class="sp-handel-w" v-if="showChangeStoreBtn || showClearAmountBtn || showClearPointBtn">
        <el-button size="mini" class="sup-mini" :disabled = "this.checkedList.length === 0" icon="el-icon-sort" @click="doChangeUserStoreHandel" v-if="showChangeStoreBtn">修改归属门店</el-button>
        <el-button size="mini" class="sup-mini" :disabled = "this.checkedList.length === 0" icon="el-icon-circle-close" @click="doClearAmountHandel" v-if="showClearAmountBtn">清除余额</el-button>
        <el-button size="mini" class="sup-mini" :disabled = "this.checkedList.length === 0" icon="el-icon-circle-close" @click="doClearPointHandel" v-if="showClearPointBtn">清除积分</el-button>
      </div>
      <el-table
        v-loading="loading"
        class="fixed-height"
        :data="tableData"
        @select="handleSelect"
        @select-all="handleSelectAll"
        empty-text="没有查询到您想要的数据"
        style="width:100%;"
        size="small">
        <el-table-column
          v-if="showChangeStoreBtn || showClearAmountBtn || showClearPointBtn"
          type="selection"
          label="全选"
          align="center"
          width="50">
        </el-table-column>
        <el-table-column min-width="80px" prop="id" label="会员编码"></el-table-column>
        <el-table-column min-width="130px" prop="inviteMemberId" label="邀请人会员编码" :formatter="emptyFormater"></el-table-column>
        <el-table-column min-width="120px" prop="phone" label="会员手机号"></el-table-column>
        <el-table-column min-width="100px" prop="name" label="姓名"></el-table-column>
        <el-table-column min-width="110px" prop="birthdayDate" label="生日" :formatter="emptyFormater"></el-table-column>
        <el-table-column min-width="70px" prop="sex" label="性别" :formatter="sexFormater"></el-table-column>
        <el-table-column min-width="120px" prop="storeName" label="注册门店" :formatter="emptyFormater"></el-table-column>
        <el-table-column min-width="120px" prop="vestStoreName" label="归属门店" :formatter="emptyFormater"></el-table-column>
        <el-table-column min-width="130px" prop="address" label="会员地址" :formatter="emptyFormater"></el-table-column>
        <el-table-column min-width="160px" prop="registerDate" label="注册时间" :formatter="emptyFormater"></el-table-column>
        <el-table-column min-width="160px" prop="firstSaleTime" label="首次消费时间" :formatter="emptyFormater"></el-table-column>
        <el-table-column min-width="120px" prop="firstSaleStoreName" label="首次消费门店" :formatter="emptyFormater"></el-table-column>
        <!-- <el-table-column min-width="110px" prop="saleAmount" label="累计消费金额"></el-table-column>
        <el-table-column min-width="80px" prop="saleTimes" label="客单量"></el-table-column>
        <el-table-column min-width="100px" prop="saleTimesPrice" label="平均客单价"></el-table-column> -->
        <el-table-column min-width="90px" prop="storeAmount" label="当前余额"></el-table-column>
        <el-table-column min-width="80px" prop="point" label="当前积分"></el-table-column>
        <el-table-column min-width="110px" prop="totalStoreAmount" label="累计充值金额"></el-table-column>
        <el-table-column min-width="80px" prop="totalPoint" label="累计积分"></el-table-column>
        <el-table-column min-width="150px" prop="tagName" label="标签" :formatter="emptyFormater"></el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination
        v-show="tableData.length>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="[20, 50, 100, 150, 200]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
    <btn-handle ref="btnHandel" :handelType="handelType" :cityOptions="cityOptions" :storeOptionsProps="storeOptionsProps" @cityChange="getStoreListForProps" @changeStore="changeStoreConfirm" :isDisabled="isDisabled" :visible="visible" @closeHandel="closeHandel" @clearAmount="clearAmountConfirm" @clearPoint="clearPointConfirm"></btn-handle>
  </div>
</template>

<script>
  import {mapMutations, mapGetters, mapActions} from 'vuex'
  import exportSheet from '@/utils/exportSheet'
  import * as utils from '@/utils/utils'
  import PageBtnHandel from './PageBtnHandel'
  import Storage from '@/utils/localStorage'
  const reqUrl = {
    // 获取门店列表接口
    search: '/plateform/a/store/condition/search',
    mSearch: '/plateform/a/mall/reportForm/member/search',
    export: '/plateform/a/mall/reportForm/member/export',
    // 修改所属门店
    store: '/member/a/shop/mem/operation/change/store',
    // 清积分
    OLPoint: '/member/a/shop/mem/operation/clear/OLPoint',
    // 清余额
    OLDeposit: '/member/a/shop/mem/operation/clear/OLDeposit'
  }
  export default {
    data() {
      return {
        keyStr: this.$route.path,
        exportLoading: false,
        loading: false,
        pageNum: 1,
        pageSize: 20,
        total: 0,
        searchContent: {
          registerStartDate: null,
          registerEndDate: null,
          phone: null,
          storeNo: null,
          minSaleAmount: null, // 累计消费金额(大于)
          maxSaleAmount: null, // 累计消费金额(小于)
          id: null, // 会员号
          maxPoint: null, // 累计消费金额(小于)
          minPoint: null // 累计消费金额(大于)
        },
        // 门店列表下拉框
        storeOptions: [],
        tableData: [],
        exportConfig: {
          id: '会员编码',
          inviteMemberId: '邀请人会员编码',
          phone: '会员手机号',
          name: '姓名',
          birthdayDate: '生日',
          sexStr: '性别',
          storeName: '注册门店',
          vestStoreName: '归属门店',
          address: '会员地址',
          registerDate: '注册时间',
          firstSaleTime: '首次消费时间',
          firstSaleStoreName: '首次消费门店',
          saleAmount: '累计消费金额',
          saleTimes: '客单量',
          saleTimesPrice: '平均客单价',
          storeAmount: '当前余额',
          point: '当前积分',
          totalStoreAmount: '累计充值金额',
          totalPoint: '累计积分',
          tagName: '标签'
        },
        checkedList: [],
        handelType: '',
        // 修改归属门店所需的门店列表
        storeOptionsProps: [],
        isDisabled: false,
        visible: false
      }
    },
    created() {
      // 获取门店
      this.getStoreList()
      this.getStoreListForProps(Storage.get('cityId'))
    },
    mounted() {
      this.doSearchHandel(true)
    },
    computed: {
      showClearAmountBtn () {
        return this.permissionMap[this.keyStr] && this.permissionMap[this.keyStr].includes('清除余额')
      },
      showClearPointBtn () {
        return this.permissionMap[this.keyStr] && this.permissionMap[this.keyStr].includes('清除积分')
      },
      showChangeStoreBtn () {
        return this.permissionMap[this.keyStr] && this.permissionMap[this.keyStr].includes('修改归属门店')
      },
      ...mapGetters(
        'common', {
          permissionMap: 'getPermissionMap',
          cityOptions: 'getCityOptions'
        }
      )
    },
    components: {
      'btn-handle': PageBtnHandel
    },
    methods: {
      emptyFormater(row, column, cellValue) {
        return cellValue || '-'
      },
      sexFormater(row, column, cellValue) {
        if(cellValue === 0) {
          return '未知'
        }else if(cellValue === 1) {
          return '男'
        }else if(cellValue === 2) {
          return '女'
        }
      },
      /**
       * [getStoreList 获取门店列表]
       * @return {[type]} [description]
       */
      getStoreList() {
        this.$http.post(reqUrl.search).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              let optionsArr = []
              for(let item of res.data) {
                let optionItem = {
                  value: item.storeNo,
                  label: item.storeNo +'-'+ item.storeName
                }
                optionsArr.push(optionItem)
              }
              optionsArr.unshift({value: null, label: '全部'})
              this.storeOptions = optionsArr
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(获取门店列表):", e.message)
        })
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      doSearchHandel(_param) {
        // 将选中数据重置为初始值
        this.checkedList = []
        this.loading = true
        if(_param === true) {
          this.pageNum = 1
        }
        this.$http.post(reqUrl.mSearch, {...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize }).then(res => {
          this.loading = false
          if(res.code === 0) {
            if(res.data && res.data.result && res.data.result.length) {
              let resultTemp = res.data.result.map((item, index) => {
                if(item.saleAmount && item.saleTimes) {
                  item.saleTimesPrice = (parseFloat(item.saleAmount) * 100) / parseFloat(item.saleTimes) / 100
                }else {
                  item.saleTimesPrice = 0
                }
                return item
              })
              this.tableData = resultTemp
            }else {
              this.tableData = []
            }

            this.total = res.data.total
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示(会员基础数据):',
            message: e.message
          })
        })
      },
      exportData() {
        this.exportLoading = true
        this.$http.post(reqUrl.export, {...this.searchContent}).then(res => {
          this.exportLoading = false
          if(res.code === 0) {
            if(res.data && res.data.result && res.data.result.length) {
              let resultTemp = res.data.result.map((item, index) => {
                if(item.saleAmount && item.saleTimes) {
                  item.saleTimesPrice = (parseFloat(item.saleAmount) * 100) / parseFloat(item.saleTimes) / 100
                }else {
                  item.saleTimesPrice = 0
                }
                if(item.sex === 0) {
                  item.sexStr = '未知'
                }else if(item.sex === 1) {
                  item.sexStr = '男'
                }else if(item.sex === 2) {
                  item.sexStr = '女'
                }
                return item
              })
              let tableDataArr = resultTemp
              exportSheet.exportFileByConfig(tableDataArr, '会员基础数据.xlsx', this.exportConfig)
              setTimeout(() => {
                this.$message.success("导出成功")
              }, 500)
            }else {
              this.$notify.warning({
                title: '温馨提示',
                message: '没有查询到要导出的数据'
              });
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.exportLoading = false
          this.$notify.error({
            title: '提示(导出数据)：',
            message: e.message
          })
        })
      },
      /**
       * [handleSelect 选择某一个]
       * @return {[type]} [description]
       */
      handleSelect(val, row) {
        let hash = {}
        let checkedListTemp = []

        if(utils.isKeyInArrayBykey(row.id, 'id', this.checkedList)) {
          this.checkedList = utils.removeItemByKeyVal('id', row.id, this.checkedList)
          return
        }else {
          checkedListTemp.push(row)
        }

        let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
        this.checkedList = checkedListTempArr.reduceRight((item, next) => {
          hash[next.id] ? '' : hash[next.id] = true && item.push(next)
          return item
        }, [])
      },
      /**
       * [handleSelectAll 选择多个]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        let hash = {}
        if(val.length > 0) {        
          // 全选
          for(let item of val) {
            this.checkedList.push(item)
          }
          // 去重
          this.checkedList = this.checkedList.reduceRight((item, next) => {
            hash[next.id] ? '' : hash[next.id] = true && item.push(next)
            return item
          }, [])
        }else { 
          // 全不选 此时val.length<=0
          // 1.获取当前table中每一条的id
          let checkedListTemp = []
          for(let item of this.tableData) {
            checkedListTemp.push(item)
          }
          // 2.从要传递的参数中去掉这些重复的
          let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'id')
          this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : [] 
        }
      },
      /**
       * [getStoreListForProps 获取门店列表]
       * @return {[type]} [description]
       */
      getStoreListForProps(_cityId) {
        this.$http.post(reqUrl.search, {DIFFCITY: true, cityId: _cityId}).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              let optionsArr = []
              for(let item of res.data) {
                let optionItem = {
                  value: item.storeNo,
                  label: item.storeNo +'-'+ item.storeName
                }
                optionsArr.push(optionItem)
              }
              this.storeOptionsProps = optionsArr
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(获取门店列表):", e.message)
        })
      },
      doChangeUserStoreHandel() {
        this.handelType = '1'
        this.visible = true
        let _params = {
          title: '修改归属门店',
          checkedList: this.checkedList
        }
        this.$refs.btnHandel.setDefauleMsg(_params)
      },
      changeStoreConfirm(_val) {
        this.isDisabled = true
        let {changeBelongStoreNo, memberIdList} = _val
        this.$http.post(reqUrl.store, {changeBelongStoreNo: changeBelongStoreNo, memberIdList: memberIdList}).then(res => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800);
          if(res.code === 0) {
            this.visible = false
            this.$message.success("修改归属门店成功")
            this.doSearchHandel()
            this.checkedList = []
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800);
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      doClearAmountHandel() {
        this.handelType = '2'
        this.visible = true
        let _params = {
          title: '确定清除以下客户的余额吗？',
          checkedList: this.checkedList
        }
        this.$refs.btnHandel.setDefauleMsg(_params)
      },
      clearAmountConfirm(_val) {
        this.isDisabled = true
        let {clearInfo} = _val
        this.$http.post(reqUrl.OLDeposit, {clearInfo: clearInfo}).then(res => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800);
          if(res.code === 0) {
            this.visible = false
            this.$message.success("清除余额成功")
            this.doSearchHandel()
            this.checkedList = []
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800);
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      doClearPointHandel() {
        this.handelType = '3'
        this.visible = true
        let _params = {
          title: '确定清除以下客户的积分吗？',
          checkedList: this.checkedList
        }
        this.$refs.btnHandel.setDefauleMsg(_params)
      },
      clearPointConfirm(_val) {
        this.isDisabled = true
        let {clearInfo} = _val
        this.$http.post(reqUrl.OLPoint, {clearInfo: clearInfo}).then(res => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800);
          if(res.code === 0) {
            this.visible = false
            this.$message.success("清除积分成功")
            this.doSearchHandel()
            this.checkedList = []
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800);
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      closeHandel() {
        this.visible = false
      }
    }
  }
</script>


