<style>

</style>
<template>
  <div>
    <el-dialog :title="title" :visible.sync="visible" width="550px" :before-close="closeHandel">
      <el-form label-width="120px" label-position="right" v-if="handelType === '1'">
        <el-form-item label="选择城市">
          <el-select v-model="cityId" filterable placeholder="请选择城市" @change="cityChange" size="small" class="search-date-picker">
            <el-option
              v-for="item in cityOptions"
              :key="item.cityId"
              :label="item.cityName"
              :value="item.cityId">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="选择门店">
          <el-select class="search-date-picker" v-model="changeBelongStoreNo" filterable placeholder="选择门店" clearable size="small">
            <el-option
              v-for="item in storeOptionsProps"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <el-table
        v-if="handelType === '2' || handelType === '3'"
        class="fixed-height"
        :data="checkedList"
        empty-text="没有查询到您想要的数据"
        max-height="320"
        size="small">
        <el-table-column min-width="80px" prop="id" label="会员编码"></el-table-column>
        <el-table-column min-width="120px" prop="phone" label="会员手机号"></el-table-column>
        <el-table-column min-width="100px" prop="name" label="姓名"></el-table-column>
        <el-table-column min-width="120px" prop="storeName" label="注册门店"></el-table-column>
        <el-table-column min-width="80px" prop="point" label="当前积分"></el-table-column>
        <el-table-column min-width="90px" prop="storeAmount" label="当前余额"></el-table-column>
      </el-table>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeHandel" size="mini">取 消</el-button>
        <el-button type="primary" @click="doConfirmHandel" size="mini" :disabled="isDisabled">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Storage from '@/utils/localStorage'
export default {
  props: {
    // handelType  '1':修改归属门店  '2':清除余额  '3':清除积分
    handelType: {
      type: String,
      value: ''
    },
    cityOptions: {
      type: Array,
      value: []
    },
    storeOptionsProps: {
      type: Array,
      value: []
    },
    isDisabled: {
      type: Boolean,
      value: false
    },
    visible: {
      type: Boolean,
      value: false
    }
  },
  data() {
    return {
      title: '',
      checkedList: [],
      cityId: Storage.get('cityId'),
      changeBelongStoreNo: ''
    }
  },
  methods: {
    /**
     * [setDefauleMsg 设置默认数据]
     */
    setDefauleMsg(_val) {
      this.title = _val.title
      this.checkedList = _val.checkedList
    },
    cityChange(_val) {
      this.changeBelongStoreNo = ''
      this.$emit("cityChange", _val)
    },
    closeHandel() {
      this.$emit("closeHandel", false)
    },
    doConfirmHandel() {
      let memberIdList = this.checkedList.map((item) => {
        return item.id
      })
      // handelType  '1':修改归属门店  '2':清除余额  '3':清除积分
      if(this.handelType === '1') {
        if(!this.cityId) {
          this.$message("请选择城市")
          return
        }
        if(!this.changeBelongStoreNo) {
          this.$message("请选择门店")
          return
        }
        let _params = {
          changeBelongStoreNo: this.changeBelongStoreNo,
          memberIdList: memberIdList
        }
        this.$emit("changeStore", _params)
      }else if(this.handelType === '2') {
        let clearInfo = this.checkedList.map((item) => {
          let _item = {
            curAmount: item.storeAmount,
            memberId: item.id
          }
          return _item
        })
        let _params = {
          clearInfo: clearInfo
        }
        this.$emit("clearAmount", _params)
      }else if(this.handelType === '3') {
        let clearInfo = this.checkedList.map((item) => {
          let _item = {
            curAmount: item.point,
            memberId: item.id
          }
          return _item
        })
        let _params = {
          clearInfo: clearInfo
        }
        this.$emit("clearPoint", _params)
      }
    }
  }
}
</script>



